.more-projects-container {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  padding-left: 10vw;
  padding-top: 5vh;
  background-color: white;
  height: 100vh;
  width: 100%;
  border-bottom: 5px dashed black;
  font-family: "Inconsolata", serif, serif;
}

.more-projects-container .more-projects-content {
  color: black;
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
}

.more-projects-container .more-projects-content .more-projects-title {
  font-size: 5vh;
  font-weight: 600;
  letter-spacing: 1px;
  text-transform: uppercase;
  /* animation: orientation both;
  animation-timeline: view(90% 50%); */
  
}

