      .more-projects-container .more-projects-content .more-projects-title .pro{
        font-size: 20vh;
        writing-mode: vertical-rl;
        color: rgb(90, 90, 90);
        border-right: 5px solid black;
      }
      
      .more-projects-container .more-projects-content .projects-list {
        list-style: none;
        padding: 0;
        padding-top: 30px;
        /* background-color: red; */
        width: 40%;
        /* border-bottom: 3px solid grey; */
}

.more-projects-container .more-projects-content .projects-list .project-item {
  font-size: 15vh;
  font-weight: 600;
  padding: 15px 0;
  transition: .5s all ease ;
  border-bottom: 3px solid grey;
  /* animation: showCase linear both;
  animation-timeline: view(); */
}

